.flex {
  display: flex
}

.flex-col {
  flex-direction: column;
}

.items {
  &--center {
    align-items: center;
  }
  &--start {
    align-items: flex-start;
  }
  &--end {
    align-items: flex-end;
  }
}

.justify {
  &--between {
    justify-content: space-between;
  }
  &--around {
    justify-content: space-around;
  }
  &--center {
    justify-content: center;
  }
}

.header {
  background-color: lightblue;
  height: 48px;
  box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 1)
}

#search-form {
  height: 32px;
  .form-input {
    border: none;
    border-radius: 6px;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    &:focus {
      outline: none
    }
    height: 100%;
  }
  .search-submit-button {
    display: inline-flex;
    background-color: #f7f4f4;
    border-top-right-radius: 6px;
    border-bottom-right-radius: 6px;
    cursor: pointer;
    height: 100%;
    &:hover {
      background-color: #d3d3d3;
    }
    img {
      margin: 0 8px;
      height: 16px;
      width: 16px;
    }
  }
}

.search-content {
  margin: 24px;
  .search-results {
    flex-wrap: wrap;

    .item-result-wrapper {
      margin-bottom: 12px;
      margin-left: 12px;
      .counters-wrapper{
        border: 1px solid #d3d3d3;
        border-top: none;
        border-radius: 4px;
        border-top-left-radius: 0;
        border-top-right-radius: 0;
        padding: 12px 12px;
        > * {
         font-size: 12px; 
        }
        .counter-item {
          .counter-title {
            font-weight: 500;
            margin-bottom: 6px;
          }
        }
      }
    }
  }
  > div {
    .load-more-button {
      margin: 24px 0;
      display: inline-flex;
      background-color: rgb(74, 94, 207);
      color: #f7f4f4;
      border-radius: 4px;
      padding: 12px 18px;
      cursor: pointer;
      display: none;
      &:hover {
        background-color: rgb(74, 74, 207);
      }
      &.show {
        display: block;
      }
    }
  }
}